.card--frame {
    position: absolute;
    width: 550px;
    height: 868px;
    left: 65px;
    top: 53px;
    font-family: 'Inter', sans-serif;
    background: #23252C;

}

.card--canvas {
    position: absolute;
    width: 317px;
    margin-top: 44px;
    margin-left: 116px;
    background: #1A1B21;
    border-radius: 10px;
}

.card--img-container {
    height: 317px;
    width: inherit;
    border-radius: 10px 10px 0px 0px;
}

.card--info {}

.card--footer {
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap:30px;
    align-items: center;
    height: 64px;
    background: #161619;
    border-radius: 0px 0px 10px 10px;
}

.card--img {
    border-radius: inherit;
    height: inherit;
    width: inherit;
}

.card--header {
    text-align: center;
}

.card--header>h1 {

    color: white;
    font-style: normal;
    font-weight: 700;
    font-size: 25px;
    line-height: 30px;
}

.role {
    margin: 0;
    font-style: normal;
    font-weight: 400;
    font-size: 12.8px;
    line-height: 15px;
    color: #F3BF99;
}

.mysite {
    font-style: normal;
    font-weight: 400;
    font-size: 10.24px;
    line-height: 150%;
    /* identical to box height, or 15px */
    color: #F5F5F5;
}

.card--buttonts {
    display: flex;
    justify-content: center;
}

.card--button {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 9px 13px 9px 11px;
    gap: 8px;

    /* shadow/sm */

    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
    border-radius: 6px;
}

.bt--left {
    padding: 9px 13px 9px 11px;
    gap: 8px;
    background: #FFFFFF;
    /* gray/300 */
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
    /* gray/300 */
    font-family: 'Inter';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    /* identical to box height, or 114% */
    /* gray/700 */
    color: #374151;
    /* Inside auto layout */
    border: 1px solid #D1D5DB;
    left: 151px;

}

.bt--right {
    margin-left: 30px;
    background: #5093E2;
    /* gray/300 */
    font-family: 'Inter';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    /* identical to box height, or 114% */
    /* gray/700 */
    color: #FFFFFF;
    border: 0px;

    /* Inside auto layout */
}

.icon--ln {
    left: 8.33%;
    right: 8.33%;
    top: 8.33%;
    bottom: 8.33%;
    color: #FFFFFF;
}


.card--textbody {

    padding-left: 40px;
    padding-right: 40px;
    color: #F5F5F5;

}

.card--textbody>h1 {
    margin:0;
    padding-top: 20px;
    font-weight: 700;
    font-size: 16px;
    line-height: 150%;
}

.card--textbody>text {
    font-weight: 400;
    font-size: 10.24px;
    line-height: 150%;
    color: #DCDCDC;
}