*{
  box-sizing:border-box;
}
.cournav {
  display: flex;
  align-items: center;
  background-color: #21222A;
  height: 90px;
  padding: 30px 25px;
}

.reactfacts {
  margin: 0;
  margin-right: auto;
  color: #61DAFB;
  font-weight: 700;
  font-size: 22px;
  letter-spacing: -0.05em;

}

.cours {
  margin: 0;
  color: #DEEBF8;
  font-weight: 600;
}

.logoimg {
  margin-right: 7px;
  height: 30px;
}


.main--header {
  margin: 0;
  font-weight: 700;
  font-size: 39px;
  letter-spacing: -0.05em;
  padding: 25px 0px;

}

.main--list {
  font-weight: 400;
  font-size: 25px;
  list-style: none;
}

.main--list>li{
  line-height: 19px;
  padding-block: 10px;
}


.main--list>li::before {
  content: "\2022";
  /* Add content: \2022 is the CSS Code/unicode for a bullet */
  color: #61DAFB;
  /* Change the color */
  font: bold;
  /* If you want it to be bold */
  font-size: xx-large;
  display: inline-block;
  /* Needed to add space between the bullet and the text */
  width: 1em;
  /* Also needed for space (tweak if needed) */
  margin-left: -1em;
  /* Also needed for space (tweak if needed) */
}

.mainbody{
  color: #DEEBF8;
  padding: 57px 27px;

}